/* Game.css */
.game-info {
    background-color: #003e6a;
    stroke: #177099;
    margin-top: 10px;
    height: 150px;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: inset 0px 0px 20px rgba(255, 255, 255, 0.3);
    cursor: pointer;
    position: relative;
    transition: all 100ms;
    opacity: 0.3;
}

.league-name {
    background-repeat: no-repeat;
    background-position: left;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    color: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
}

.game-home-name,
.game-away-name {
    position: absolute;
    display: block;
    top: 100px;
    transform: translateY(-50%);
    color: #ffffff;
    font-weight: 600;
    font-size: 16px;
}

.game-home-name {
    right: calc(50% + 95px); /* 오른쪽으로 이동 */
    text-align: right;
}

.game-away-name {
    left: calc(50% + 95px); /* 왼쪽으로 이동 */
    text-align: left;
}

.game-datetime {
    font-size: 16px;
    color: #ffffff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center; /* 텍스트를 가운데 정렬 */
    margin-top: 10px; /* 상단 여백 추가 */
}

.game-home-icon,
.game-away-icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 40px;
    height: 40px;
}

.game-home-icon {
    left: 20px;
}

.game-away-icon {
    right: 20px; /* 오른쪽으로 이동 */
}

.game-home-score,
.game-away-score,
.game-vs-score {
    position: absolute;
    font-size: 20px;
    font-weight: bold;
    color: #ffffff;
    top: 50%;
    transform: translateY(-50%);
}

.game-home-score {
    left: 75px;
}

.game-away-score {
    right: 75px;
}

.game-vs-score {
    left: 50%;
    transform: translateX(-50%);
}

/* Game.css */

.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* 배경을 반투명한 검은색으로 설정 */
    z-index: 1;
}

.modal-content {
    background-color: #fff;
    border: 2px solid #333; /* 테두리 추가 */
    border-radius: 10px; /* 테두리의 모서리를 둥글게 만듦 */
    padding: 20px;
    max-width: 600px; /* 모달의 최대 너비 설정 */
    width: 80%; /* 모달의 너비를 화면 너비의 80%로 설정 */
    text-align: center;
}

.modal-close {
    background-color: #333;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}
